import React from 'react'
import { Form } from 'react-bootstrap'

const Filter = (props) => {
  return (
    <Form>
      <Form.Group>
        <Form.Label>Filter:</Form.Label>
          <Form.Control
            value={props.value}
            onChange={props.onChange}
          />
      </Form.Group>
    </Form>
  )
}

export default Filter