import React from 'react'

const Person = ({ persons, deletePersons }) => {
  
  return (
    persons.map(person =>
      <tr key={person.id}>
        <td>
          {person.name}
        </td> 
        <td>
          {person.number}
        </td>
        <td>
          <button onClick={() => deletePersons(person.id)}>delete</button>
        </td>
      </tr>
    )
  )
}

export default Person